/* eslint-disable */
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    SystemConfig : 0,
  },
  mutations:{
    SetNitroSysConfig (SystemConfig)
    {
      state.SystemConfig = 0;
    },
    SetNitroSysPlusConfig (SystemConfig)
    {
      state.SystemConfig = 1;
    },
    SetAutoShotConfig (SystemConfig)
    {
      state.SystemConfig = 2;
    }
  },
  actions: {

  },
  modules: {
  }
})
