export 
<template>
<span>
    <v-container class="mt-0 pt-0 mb-0 pb-0" fluid>
    <v-row class="flex-nowrap" justify="center" align="center">
        <v-col class="mt-0 pa-0" justify="center" align="center">
            <v-row class="text-center" align="center" justify="center">
                <h1 :class="Color+'--text'">{{TitleProp}}</h1>
            </v-row>
            <v-scale-transition>
                <v-row v-if="selected" align="center" justify="center" >
                    <v-col>
                    <v-slider v-model="TempTarget" min="70" max="200" hide-details>
                    </v-slider>
                     <v-btn dark :color="ZoneBtnColor" @click="ToggleZoneEnable">{{ZoneEnabled}}</v-btn>
                    </v-col>
                </v-row>
            </v-scale-transition>
                <v-row align="start" justify="center" class="mt-2 pt-0">
                    <v-btn v-if="selected" icon class="grey mr-1" @click="Decrement"><v-icon>mdi-minus</v-icon></v-btn>                
                    <v-btn :outlined="selected" class="grey lighten-2" @click="AdjustBtnClick">
                        <h1>{{UpdateTempTarget}}째F</h1>
                    </v-btn>
                    <v-btn v-if="selected" icon class="grey ml-1" @click="Increment"><v-icon>mdi-plus</v-icon></v-btn> 
                </v-row>
            <v-scale-transition>
                <v-row v-if="selected" align="top" justify="center" class="mt-1 pt-1">
                        <v-btn class="red mr-5" @click="selected=false" icon><v-icon>mdi-close</v-icon></v-btn>
                        <v-btn class="green" @click="SaveBtnClick" icon ><v-icon>mdi-check</v-icon></v-btn>           
                </v-row>
            </v-scale-transition>
                <v-row align="center" justify="center">
                    <h1>{{TempFeedbackProp}}째F
                    </h1>
                    <v-badge inline bordered :color="SSRActiveColor"></v-badge>
                </v-row>
        </v-col>
    </v-row>

    </v-container>
</span>
</template>

<script>
export default {
    name:'TempAdjustWidget',
    data () 
    {
        return {
            selected:false,
            TempTarget:0,
            interval:{},
            SSRActiveColor:'grey',
        }
    },
    props:{
        TempTargetProp:Number,
        TempFeedbackProp:Number,
        ZoneEnableProp:Boolean,
        ZoneID: Number,
        ParameterID:String,
        TitleProp:String,
        Color:String,
    },
    methods:{
        AdjustBtnClick()
        {
            this.selected = !this.selected;
            if(this.selected)
            {
                this.TempTarget = this.TempTargetProp;
            }
        },
        Increment(){
            this.TempTarget++;
        },
        Decrement(){
            this.TempTarget--;
        },
        ToggleZoneEnable(){
            let enable = this.ZoneEnableProp == 1 ? 0 : 1;
            let toggleJsonObj = {"WSP":{"WSPID":this.ZoneID,"WSPUnits":1,"WSPVal":enable.toString()}};
            this.$emit('toggleClicked', toggleJsonObj);
        },
        SaveBtnClick()
        {
            var jsonObj = {"WSP":{"WSPID":this.ParameterID,"WSPUnits":1,"WSPVal":this.TempTarget.toString()}};
            this.$emit('saveClicked', jsonObj);
            this.selected = false;
        }
    },
    computed:{
        UpdateTempTarget: function(){
            if (!this.selected)
            {
                return this.TempTargetProp;
            }
            else
            {
                return this.TempTarget;
            }
        },
        ZoneEnabled :function()
        {
            return this.ZoneEnableProp == 1 ? "On" : "Off";
        },
        ZoneBtnColor :function()
        {
            return this.ZoneEnableProp == 1 ? "green" : "red";
        }
    },
    beforeMount()
    {        
        this.TempTargetProp = 170;
        this.TempFeedbackProp = 150;
    }
}
</script>

<style>

</style>