import Vue from 'vue'
import VueRouter from 'vue-router'
import TempControl from '../views/TempControl.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'TempControl',
    component: TempControl
  },
  {
    path: '/YieldCalc',
    name: 'YieldCalc',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/YieldCalc.vue')
  },
  {
    path: '/AutoShot',
    name: 'AutoShot',
    component: () => import(/* webpackChunkName: "about" */'../views/AutoShot.vue')
  },

]

const router = new VueRouter({
  base: '/NS/',
  routes
})

export default router
