<template>
<span>
    <v-app-bar  class="green" app dark>
        <v-btn @click="drawer = !drawer" icon><v-icon>mdi-menu</v-icon></v-btn>
        <v-img v-if="RouteIsAutoshot" @click="drawer = !drawer"  class="ml-n9" src="../assets/AutoshotTitleLogo2.png" :height="ImageHeight" contain></v-img>
        <v-img v-else @click="drawer = !drawer"  class="ml-n9" src="../assets/logomedium.png" :height="ImageHeight" contain></v-img>
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" app class="white">
        <v-list nav>
        <v-list-item v-for="item in items"
          :key="item.title"
          link
          v-bind:to="item.link">
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        </v-list>
    </v-navigation-drawer>
</span>
</template>

<script>
/* eslint-disable */
import { mdiBullseye } from '@mdi/js';
export default {
        name: 'AppNavigation',
        data () {
            return {
            appTitle: 'NitroSys Control Application',
            drawer: false,
            items:[{title:'Temperature Control', icon: 'mdi-thermometer', link:'/'}, 
                  {title:'Yield Calculator', icon: 'mdi-calculator', link:'/YieldCalc'},
                  {title:'Autoshot', icon: mdiBullseye, link:'/AutoShot'}],
            prominent:false,
            configDone:false,
        }
    },
  computed:
  {
    PageTitle: function()
    {
      if (this.$route.name === 'AutoShot')
      {
        return "Autoshot";
      }
      else if (this.$route.name === 'YieldCalc')
      {
        return "Yield Calculator";
      }
      else
      {
        return 'Spray Foam Systems';
      }
    },
    ImageHeight: function() 
    {
        console.log(this.$vuetify.breakpoint.name);
        if (this.$route.name === 'AutoShot')
        {
            switch (this.$vuetify.breakpoint.name) {
              case 'xs': return '50px'
              case 'sm': return '50px'
              case 'md': return '60px'
              case 'lg': return '60px'
              default: return '60px'
          }
        }
        else{
            switch (this.$vuetify.breakpoint.name) {
              case 'xs': return '55px'
              case 'sm': return '55px'
              case 'md': return '65px'
              case 'lg': return '65px'
              default: return '65px'
          }
        }
    },
    RouteIsAutoshot: function()
    {
      return this.$route.name === 'AutoShot';
    }

  },
  methods:
  {
    RequestConfiguration()
    {
      this.$socket.sendObj({"WGPG" : "Application_Configuration"});
    },
    RecieveMessage(evt)
    {
      try {
            if (evt.data.includes("SystemConfig", 0) && !this.configDone)
            {

              let strData = evt.data;
              let nlIndex = strData.indexOf('\n');

              if (nlIndex > 0)
              {
                strData = strData.substring(0, nlIndex);
              }

              let dataObj = JSON.parse(strData);
              
              let SystemConfig = 0;

              dataObj.Values.forEach(function(element){
                SystemConfig = element.MGP.ParamVal;
              });
              switch (dataObj.MGP.ParamVal)
              {
                case 0:
                    this.items = [{title:'Temperature Control', icon: 'mdi-thermometer', link:'/'}, 
                                {title:'Yield Calculator', icon: 'mdi-calculator', link:'/YieldCalc'}];
                    if (this.$route === '/AutoShot')
                    {
                      this.$router.push({path: '/'});
                    }
                    break;
                case 2:
                    if (this.$route !== '/AutoShot')
                    {
                      this.$router.push({path: '/AutoShot'});
                    }
                    this.items = [{title:'Autoshot', icon: 'mdi-engineering', link:'/AutoShot'}];
                default:
                    this.items = [{title:'Temperature Control', icon: 'mdi-thermometer', link:'/'}, 
                                  {title:'Yield Calculator', icon: 'mdi-calculator', link:'/YieldCalc'},
                                  {title:'Autoshot', icon: mdiBullseye, link:'/AutoShot'}];
                    break;
              }       
              this.configDone = true;
            }
          } 
          catch (error)
          {
            console.log(error);
          }
    }
  },
  beforeMount()
  {
        this.$options.sockets.onopen = () =>
        {
          this.Connected = true;
          this.RequestConfiguration();
        },
        this.$options.sockets.onclose = () =>
        {
          this.Connected = false;
        },
        this.$options.sockets.onmessage = (evt) =>
        {
          this.RecieveMessage(evt);
        }
  }

}
</script>

<style scoped>

</style>