
<template>
    <span>
        <v-container class="pt-2">
            <v-row align="stretch" justify="center" class="flex-sm-nowrap">
                <v-col cols="12" sm="4" class="mt-0 pt-0">
                    <v-card>
                        <v-container class="mt-0 pt-0 pb-1">
                            <v-row justify="center" no-gutters class="flex-nowrap">
                                <v-col cols="8" sm="12" class="flex-nowrap">
                                    <TempAdjustWidget @toggleClicked="SendToggleMsg" @saveClicked="SendSaveMsg" :ZoneID="136" :ZoneEnableProp="TempData.PreheatAEnabled" :TempTargetProp="TempData.vPreheatTempA_Target" :TempFeedbackProp="TempData.vPreheatTempA_F" ParameterID='59' TitleProp='A' Color='red'></TempAdjustWidget>
                                </v-col>
                            </v-row>
                            <v-row>
                                <v-divider class="ml-3"></v-divider>
                            </v-row>
                            <v-row justify="center">
                                <h1 class="red--text">{{InletPressureA}} PSI</h1>
                            </v-row>
                        </v-container>
                    </v-card>
                </v-col>
                <v-col cols="12" sm="4" class="mt-0 pt-0">
                    <v-card >
                        <v-container class="mt-0 pt-0 pb-1">
                        <v-row justify="center" no-gutters class="flex-nowrap">
                                <v-col cols="8" sm="12" class="flex-nowrap">
                                    <TempAdjustWidget @toggleClicked="SendToggleMsg" :ZoneID="137" :ZoneEnableProp="TempData.HoseEnabled" @saveClicked="SendSaveMsg" :TempTargetProp="TempData.vHoseTempA_Target" :TempFeedbackProp="TempData.vHoseTempA_F" ParameterID='61' TitleProp='Hose' Color='green'></TempAdjustWidget>
                                </v-col>
                        </v-row>
                        </v-container>
                    </v-card>
                </v-col>
                <v-col cols="12" sm="4" class="mt-0 pt-0">
                    <v-card>
                        <v-container class="mt-0 pt-0 pb-1">
                            <v-row justify="center" no-gutters class="flex-nowrap">
                                <v-col cols="8" sm="12">
                                    <TempAdjustWidget @toggleClicked="SendToggleMsg" :ZoneID="138" :ZoneEnableProp="TempData.PreheatBEnabled" @saveClicked="SendSaveMsg" :TempTargetProp="TempData.vPreheatTempB_Target" :TempFeedbackProp="TempData.vPreheatTempB_F" ParameterID='60' TitleProp='B' Color='blue'></TempAdjustWidget>
                                </v-col>                             
                            </v-row>
                            <v-row>
                                <v-divider></v-divider>
                            </v-row>
                            <v-row justify="center">
                                <h1 class="blue--text">{{InletPressureB}} PSI</h1>
                            </v-row>
                        </v-container>
                    </v-card>
                </v-col>
            </v-row>
        </v-container>
    </span>
</template>

<script>
/* eslint-disable */
import TempAdjustWidget from '../components/TempAdjustWidget.vue'
export default {
    name: 'TemperatureControls',
    data () {
        return {
            hoseClass:"mt-0 pt-0 pl-0 pr-0 ml-0 mr-0",
            adjustInterval:false,
            Connected:false,
            switchVal:false,
            tempChangeReplyReceived: true,
            heatToggleReplyReceived: true,
            currTempChangeObj: {},
            currToggleObj: {},
            TempData:{}
        }
    },
    components:
    {
        TempAdjustWidget,
    },
    computed:
    {
        InletPressureA: function()
        {
            if (this.TempData.vInletPressureA_PSI == 'NULL')
            {
                this.TempData.vInletPressureA_PSI = 0;
            }
            return this.TempData.vInletPressureA_PSI;
        },
        InletPressureB: function()
        {
            if (this.TempData.vInletPressureB_PSI == 'NULL')
            {
                this.TempData.vInletPressureB_PSI = 0;
            }
            return this.TempData.vInletPressureB_PSI;
        }
    },
    sendTime:0,
    methods:
    {
        GetTempData() 
        {
          this.sendTime = new Date().getTime();
          this.$socket.sendObj({"WGPG" : "Application_TempControlPage"});
        },
        RecieveMessage(evt)
        {         
          try
          {
              
            if (evt.data.length > 1 && evt.data.includes("Application_TempControlPage"))
            {
              let strData = evt.data;
              let nlIndex = strData.indexOf('\n');
              if (nlIndex > 0)
              {
                strData = strData.substring(0, nlIndex);
              }
            
              let dataObj = JSON.parse(strData);
              let tData = {};
              dataObj.Values.forEach(function(element){
                tData[element.MGP.MGPLabel] = element.MGP.ParamVal;
              });
              this.TempData = tData;
            }
            else if (evt.data.includes("MSP"))
            {

              let strData = evt.data;
              let nlIndex = strData.indexOf('\n');
              if (nlIndex > 0)
              {
                strData = strData.substring(0, nlIndex);
              }
            
              let dataObj = JSON.parse(strData);
              
              if (dataObj.MSP.status == 3)
              {
                if (dataObj.MSP.MSPID >= 136 && dataObj.MSP.MSPID <= 138)
                {
                    console.log("Heat toggle reply received.")
                    this.heatToggleReplyReceived = true;
                }
                else
                {
                  this.tempChangeReplyReceived = true;
                }
              }
            }
          }
          catch(Exception)
          {
            console.log(Exception);
            console.log(evt.data.length);
            console.log(evt.data.indexOf('\n'));
            console.log(evt.data);
          }
          if(!this.tempChangeReplyReceived)
          {
              this.$socket.sendObj(this.currTempChangeObj);
          }
          if(!this.heatToggleReplyReceived)
          {
              this.$socket.sendObj(this.currToggleObj);
          }
          let interval = new Date().getTime() - this.sendTime;
          if (interval > 200)
          {
            interval = 199;
          }
          setTimeout(this.GetTempData(), 200 - interval)
        },
        SendSaveMsg(jsonObj)
        {
            this.currTempChangeObj = jsonObj;
            try
            {
                this.$socket.sendObj(jsonObj);
                this.tempChangeReplyReceived = false;
            }
            catch (Exception)
            {
                console.log(Exception);
            }
        },
        SendToggleMsg(toggleJsonObj)
        {
            this.currToggleObj = toggleJsonObj;
            try
            {
                this.$socket.sendObj(toggleJsonObj);
                this.heatToggleReplyReceived = false;
            }
            catch (Exception)
            {
                console.log(Exception);
            }
        }
    },
    beforeMount ()
    {
        if (this.Connected)
        {
         this.GetTempData();
        }
        this.$options.sockets.onopen = () =>
        {
          this.GetTempData();
          this.Connected = true; 
        },
        this.$options.sockets.onclose = () =>
        {
          this.Connected = false;
        },
        this.$options.sockets.onmessage = (evt) =>
        {
          this.RecieveMessage(evt);
        }
    },     
}
</script>